import {Component} from 'react'
import './root.css'
class root extends Component{
    constructor(props) {
        super(props);

        this.state = {
            token:"",
            url:"",
            shortUrl:"",
            redirectUrl:""
        }
        this.onTypeShortUrl = this.onTypeShortUrl.bind(this)
        this.onTypeToken = this.onTypeToken.bind(this)
        this.onTypeUrl = this.onTypeUrl.bind(this)
        this.checkKey = this.checkKey.bind(this)

    }
    onTypeToken=(e)=> {
        this.setState({token:e.target.value})
    }

    onTypeShortUrl=(e) => {
        this.setState({shortUrl:e.target.value})
    }
    onTypeUrl=(e)=> {
        this.setState({url:e.target.value});
    }
    checkKey=()=>{
        const baseurl = `https://shortner.nyaator.co/n?token=${this.state.token}&k=${this.state.shortUrl}&u=${this.state.url}`
        fetch(baseurl)
            .then(res=>{
                res.json()
                    .then(res=>{
                        if(res===true){
                            this.setState({redirectUrl:`${document.location.href}${this.state.shortUrl}`})
                        }
                        else{
                            this.setState({redirectUrl:"Unable to process request."})
                        }
                    })
            })

    }
    render(){
        return(
            <div className="App">
                <div className="name">
                    <h1>URL Shortner</h1>
                </div>
                <div className="main">
                    <div className="view">
                        <div className='token'>
                            <b>Token</b>
                            <input type='password' onChangeCapture={this.onTypeToken}/>
                        </div>
                        <div className='url'>
                            <b>Url </b>
                            <input type='text' onChangeCapture={this.onTypeUrl}/>
                        </div>
                        <div className='short'>
                            <b>ShortUrl</b>
                            <input type='text' onChange={this.onTypeShortUrl}/>
                        </div>
                        <button type='submit' className='submitButton' onClick={this.checkKey}>Shorten URL</button>
                    </div>
                    <div className='result'>
                        {this.state.redirectUrl}
                    </div>
                </div>
                <footer>
                    <div className="info">
                        Build using ReactJS and Python.  <a href="https://github.com/kazuma-0"> GitHub  </a>
                    </div>
                </footer>
            </div>
        )
    }
}
export  default  root;