import {Component} from 'react'
import ErrorPage from "./404";

class Redirect extends Component{
    constructor(props) {
        super(props);
        this.state = {
            def:false
        }
        this.componentDidMount = this.componentDidMount.bind(this)
    }
    componentDidMount() {
        const baseurl = `https://shortner.nyaator.co/r?&key=${this.props.match.params.key}`
        fetch(baseurl)
            .then(res=>{res.json()
                .then(re=>{
                    console.log(re)
                    if(re !==false){
                        console.log(re)
                        window.location.replace(re)
                    }
                    else{
                        this.setState({def:true})
                    }
                })
            })
    }

    render() {
        const showError  = this.state.def
        return(
            <div>
                {showError ? (
                    <ErrorPage />) :( <div className='rp'>Redirecting...</div>)
                }
            </div>
        )
    }

}

export default Redirect;